import React from 'react';
import {Icon} from "../Icon/Icon";
import {Body4_Regular} from "../Typography/Typography";
import styled from "styled-components";
import {theme} from "../../styles/theme";


export default function Advantages() {
    return (
        <ContentWrapper>
            <Content>
                <Icon srcImg={"trophy.svg"}></Icon>
                <Text>Только проверенные объекты для вашего спокойствия. Сотни тысяч отзывов, фото и видео от
                    реальных постояльцев. Безопасное бронирование и достоверная информация</Text>
            </Content>
            <Content>
                <Icon srcImg={"like.svg"}></Icon>
                <Text>Ваше идеальное жильё всего в нескольких кликах. Наша передовая система поиска и
                    настраиваемые фильтры моментально находят лучшие предложения,
                    соответствующие вашим критериям</Text>
            </Content>
            <Content>
                <Icon srcImg={"location.svg"}></Icon>
                <Text>У Arendomania более 2 000 000 вариантов размещения по всей России.
                    Это отели, апартаменты, квартиры. В любой сезон вы найдёте то,
                    что подходит именно вам.</Text>
            </Content>
            <Content>
                <Icon srcImg={"message.svg"}></Icon>
                <Text>Заботливая поддержка 24/7. Наши операторы помогут с выбором отеля и бронированием.
                    Если вопрос возник в поездке, оператор будет на связи и найдёт решение
                    в кротчайший срок.</Text>
            </Content>
        </ContentWrapper>
    );
};

const ContentWrapper = styled.div`
    max-width: 1240px;
    width: 100%;
    display: flex;
    align-items: flex-start;
    @media ${theme.media.tablet} { 
        /**адаптивная верстка 'screen and (max-width: 992px)',*/
        align-items: center;
        flex-direction: column;
    }
        @media ${theme.media.mobile} {
            /**адаптивная верстка 'screen and (max-width: 576px)',*/
            flex-direction: column;
            align-items: center;
            gap: 32px;
        }
`;

const Content = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 36px;
    text-align: center;

    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        gap: 32px;
    }
`;

const Text = styled(Body4_Regular)`
    padding: 10px;
    text-align: center;

    @media ${theme.media.mobile} {
        /**адаптивная верстка 'screen and (max-width: 576px)',*/
        max-width: 382px;
    }
`;
