import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import CardActionArea from "@mui/material/CardActionArea";
import { theme } from "../../../styles/theme";
import {
  Body2_Bold,
  Body3_Regular,
  Body4_Bold,
  Body6_Regular,
} from "../../Typography/Typography";
import { useSelector } from "react-redux";
import { selectorAdaptation } from "../../../store/selectors/selectorAdaptation";

type Props = {
  title: string;
  description: string;
  numberOfLines: number;
  img: string;
};
export default function CardObject({
  title,
  description,
  numberOfLines,
  img,
}: Props) {
  const isDesktop = useSelector(selectorAdaptation);
  const maxLength =
    numberOfLines === 1
      ? 35
      : numberOfLines === 2
      ? 73
      : numberOfLines === 3
      ? 103
      : 103;

  if (description.length > maxLength) {
    description = description.slice(0, maxLength) + " ...";
  }
  return (
    <Card
      sx={{
        height: "100%",
        borderRadius: isDesktop ? "20px" : "12px",
        color: "#FDFDFD",
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-start",
      }}
    >
      <CardActionArea
        sx={{
          display: "flex",
          flexDirection: "column",
          height: "100%",
        }}
      >
        <CardMedia
          component="img"
          height={isDesktop ? "257px" : "120px"}
          width={isDesktop ? "392px" : "174px"}
          image={img}
          alt={title}
          sx={{
            flexShrink: 0,
          }}
        />
        <CardContent
          sx={{
            display: "flex",
            boxSizing: "border-box",
            flexDirection: "column",
            alignItems: "center",
            backgroundColor: theme.colors.primary80,
            padding: isDesktop ? "20px 22px 28px 22px" : "5px 9px 13px 9px",
            flexGrow: 1,
            width: "100%",
          }}
        >
          {isDesktop ? (
            <Body2_Bold color={"white"}>{title}</Body2_Bold>
          ) : (
            <Body4_Bold color={"white"}>{title}</Body4_Bold>
          )}
          {isDesktop ? (
            <Body3_Regular style={{ textAlign: "center" }} color={"white"}>
              {description}
            </Body3_Regular>
          ) : (
            <Body6_Regular style={{ textAlign: "center" }} color={"white"}>
              {description}
            </Body6_Regular>
          )}
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
