"use client";
import {directionData, hotelsData} from "./data";
import Advantages from "./components/advantages/advantages";
import Subscription from "./components/Subscription/Subscription";

import {CarouselObject} from "./components/carousel/CarouselObject";
import {useSelector} from "react-redux";
import {RootState} from "./store/store";
import {CircularProgress} from "@mui/material";
import {selectorAdaptation} from "./store/selectors/selectorAdaptation";
import styled from "styled-components";

export default function Home() {
    const isDesktopStore = useSelector(selectorAdaptation);

    const {isLoading} = useSelector((state: RootState) => state.registration);
    return (
        <div
            style={{
                maxWidth: "1440px",
                width: "100%",
                display: "flex",
                gap: "100px",
                flexDirection: "column",
                alignItems: "center",
            }}
        >
            {/*добавил прогресс*/}
            {isLoading && (
                <Loading>
                    <CircularProgress size="50px"/>
                </Loading>
            )}
            <div
                style={{
                    height: "498px",
                    maxWidth: "1440px",
                    width: "100%",
                    backgroundColor: "antiquewhite"
                }}
            >
                {/*<BackDrop />*/}
            </div>
            <div
                style={{
                    maxWidth: isDesktopStore ? "1240px" : "768px",
                    width: "100%",
                    display: "flex",
                    gap: "100px",
                    flexDirection: "column",
                }}
            >
                <Advantages/>
                <CarouselObject
                    data={directionData}
                    title="Популярные направления"
                    numberOfLinesСard={1}
                />
                <Subscription/>
                <CarouselObject
                    data={hotelsData}
                    title="Лучшие отели"
                    numberOfLinesСard={3}
                />
            </div>
        </div>
    );
}

const Loading = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.5); //затемнение основного экрана при открытии
`;